import { post } from '@/utils/request.js'
const state = {
  userTel: "",
  userInfo: null,
  operation: []
};
// 赋值方法    
const mutations = {
  userTelChange: (state, value) => {
    state.userTel = value
  },
  userInfoChange: (state, value) => {
    state.userInfo = value
  },
  operationChange: (state, value) => {
    state.operation = value
  }
};
//调用方法
const actions = {
  submitTel ({ commit }, value) {
    commit('userTelChange', value)
  },
  async submitUserInfo ({ commit }) {
    let resData = await post('cgi/get_mchinfo');
    if (resData.ret_code == 0) {
      commit('userInfoChange', resData.ret_data)
    }
  },
  set_operation ({ commit }, value) {
    commit('operationChange', value)
  }
};
export default {
  namespaced: true,
  state,
  mutations,
  actions
}