import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
export default new Router({
  // mode: 'history',
  base: '/pay',
  routes: [
    {
      path: '/',
      redirect: '/login',
      component: resolve => require(["@/views/main.vue"], resolve),
      children: [
        { path: '/home', name: 'home', component: resolve => require(["@/views/home/index.vue"], resolve) },
        { path: '/msg', name: 'msg', component: resolve => require(["@/views/msg/index.vue"], resolve) },
        { path: '/charge', name: 'charge', component: resolve => require(["@/views/charge/index.vue"], resolve) },
        { path: '/new-task', name: 'new-task', component: resolve => require(["@/views/msg/new-task.vue"], resolve) },
        { path: '/chargePage', name: 'chargePage', component: resolve => require(["@/views/charge/chargePage.vue"], resolve) },
      ]
    },
    {
      path: '/login',
      name: 'login',
      component: resolve => require(["@/views/login/login.vue"], resolve)
    },
    {
      path: '/register',
      name: 'register',
      component: resolve => require(["@/views/login/register.vue"], resolve)
    },
    {
      path: "/forget",
      name: 'forget',
      component: resolve => require(["@/views/login/forget.vue"], resolve)
    }
  ]
})


