import axios from 'axios';
import QS from 'qs';
import localStore from '@/utils/localstore.js'
import ViewUI from 'view-design'
import router from '@/router'
import md5 from 'js-md5'
import configUrl from '@/config/index.js'
axios.defaults.baseURL = configUrl.baseURL;
// const baseUrl = configUrl.baseURL;
// if (process.env.NODE_ENV == 'development') {
//   axios.defaults.baseURL = '/api';
// }
// else if (process.env.NODE_ENV == 'production') {
//   axios.defaults.baseURL = baseUrl;
// }


axios.defaults.timeout = 10000;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
// 请求拦截器
axios.interceptors.request.use(
  config => {
    // 每次发送请求之前判断vuex中是否存在token        
    // 如果存在，则统一在http请求的header都加上token，这样后台根据token判断你的登录情况
    // 即使本地存在token，也有可能token是过期的，所以在响应拦截器中要对返回状态进行判断 
    const token = localStore.get('token');
    token && (config.headers.token = token);
    return config;
  },
  error => {
    return Promise.error(error);
  }
)
// 响应拦截器
axios.interceptors.response.use(
  response => {
    // 如果返回的状态码为200，说明接口请求成功，可以正常拿到数据     
    // 否则的话抛出错误
    if (response.status === 200) {
      return Promise.resolve(response);
    } else {
      return Promise.reject(response);
    }
  },
  // 服务器状态码不是2开头的的情况
  // 这里可以跟你们的后台开发人员协商好统一的错误状态码    
  // 然后根据返回的状态码进行一些操作，例如登录过期提示，错误提示等等
  // 下面列举几个常见的操作，其他需求可自行扩展
  error => {
    if (error.response.status) {
      switch (error.response.status) {
        // 401: 未登录
        // 未登录则跳转登录页面，并携带当前页面的路径
        // 在登录成功后返回当前页面，这一步需要在登录页操作。                
        case 401:
          ViewUI.Message.destroy()
          ViewUI.Message.error('您的登录会话已失效, 请重新登录')
          toLogin()
          break;
        case 400:
          ViewUI.Message.destroy()
          ViewUI.Message.error('您的登录会话已失效, 请重新登录')
          toLogin()
          break;
        case 404:
          ViewUI.Message.destroy()
          ViewUI.Message.error('您的登录会话已失效, 请重新登录')
          toLogin()
          break;
      }
      return Promise.reject(error.response);
    }
  }
)
function toLogin () {
  router.replace({
    path: '/login',
    query: {
      redirect: router.currentRoute.fullPath
    }
  });
}
/**
 * get方法，对应get请求
 * @param {String} url [请求的url地址]
 * @param {Object} params [请求时携带的参数]
 */
export function get (url, params) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
      params: params
    }).then(res => {
      resolve(res.data);
    }).catch(err => {
      reject(err.data)
    })
  })
}
/** 
 * post方法，对应post请求 
 * @param {String} url [请求的url地址] 
 * @param {Object} params [请求时携带的参数] 
 */
export function post (url, params, strzh = true) {

  let jstr = params ? JSON.stringify(params) : ''
  let data = params
  if (strzh) {
    data = {
      self_id: localStore.get('self_id') ? localStore.get('self_id') : 0,
      idempotent: new Date().getTime(),
      sign: jstr ? md5(jstr) : '',
      data: jstr,
      sess: localStorage.getItem('sess')
    }
  }
  let reg = /(signin|signup|get_code|change_passwd)/
  if (!localStore.get('self_id') && !reg.exec(url)) {
    return router.push('/login')
  }

  return new Promise((resolve, reject) => {
    axios.post(url, QS.stringify(data))
      .then(res => {
        if (res.data.ret_code < 0) {
          ViewUI.Message.error(res.data.ret_msg)
        }
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data)
      })
  });
}

export function put (url, params) {
  return new Promise((resolve, reject) => {
    axios.put(url, QS.stringify(params))
      .then(res => {
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data)
      })
  });
}