import { post } from '@/utils/request.js'
const state = {
  groups: null
};
// 赋值方法    
const mutations = {
  groupsChange: (state, value) => {
    state.groups = value
  }
};
//调用方法
const actions = {
  setGroups ({ commit }, value) {
    commit('groupsChange', value)
  },
  getGroups ({ commit }) {
    post('cgi/get_group').then(function (data) {
      if (data.ret_code == 0) {
        commit('groupsChange', data.ret_data)
      }
    })
  }
};
export default {
  namespaced: true,
  state,
  mutations,
  actions
}