<template>
  <div>
    <div class="upload-btn-box">
      <div @click="choiceBtn">
        <slot></slot>
        <span v-if="showFileName" class="ml-10">{{fileName}}</span>
      </div>
      <input ref="filElem" type="file" class="upload-file" :accept="acceptStr" @change="getFile">
    </div>
  </div>
</template>
<script>

export default {
  props: {
    excel: {
      type: Boolean,
      default: false
    },
    showFileName: {
      type: Boolean,
      default: false
    },
    txt: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      outputs: [],
      fileName: ''
    }
  },
  computed: {
    acceptStr () {
      if (this.excel) {
        return 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel'
      }
      else if (this.txt) {
        return 'text/plain'
      } else {
        return ''
      }
    }
  },
  methods: {
    getFile (event) {
      // const files = event.target.files;
      // let fileType = files[0].name.split('.')[1]
      // if (this.excel) {
      //   if (fileType != 'xlsx' && fileType != 'xls') {
      //     return this.$Message.error('请选择Excel文件')
      //   }
      // }
      // if (this.txt) {
      //   if (fileType != '.txt') {
      //     return this.$Message.error('请选择txt文件')
      //   }
      // }
      this.fileName = event.target.files[0].name
      this.$emit('getFile', event)
      this.reset()
    },
    choiceBtn () {
      this.$refs.filElem.dispatchEvent(new MouseEvent('click'))
    },
    reset () {
      this.$refs.filElem.value = ''
      this.fileName = ''
    }
  }
}
</script>
<style lang="less" scoped>
.upload-file {
  display: none;
}
</style>